<script>
export default {
  data() {
    return {
      productsList: [],
      baseURL: 'http://drupalwebservices79qvukp3ah.devcloud.acquia-sites.com'
    }
  },
  methods: {
    loadProduct(e) {
      // Todo: Create one file for config. E.g: config.js.
      // Todo: Check basic auth is only for application.
      this.$http.get('/api/products/' + e)
        .then((res) => {
          this.productsList = res.data
        })
    }
  },

  created() {
    this.loadProduct( this.$route.params.id );
  }
}
</script>

