<template>
  <div class="about">
    <h1 class="mb-0"><font-awesome-icon icon="code" /> Developers</h1>
    <hr>
      <div class="row" >
        <div class="col-2" v-for="(user) in usersList" :key="user.uid">
          <img :src="baseURL + user.user_picture" class="img-fluid rounded-circle">
          <p class="text-center mt-3 text-primary">{{user.field_firstname}} {{user.field_familyname}}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      usersList: [],
      // Todo: Create one file for config. E.g: config.js.
      baseURL: 'http://drupalwebservices79qvukp3ah.devcloud.acquia-sites.com'
    }
  },
  methods: {
    loadProduct() {
      this.$http.get('/api/users')
        .then((res) => {
          this.usersList = res.data
        })
    }
  },
  created() {
    this.loadProduct()
  }
}

</script>
